import { dbService } from "fbase";
import React, { useEffect, useState } from "react";
import Chat from "components/Chat";
import ChatFac from "components/ChatFac";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faToggleOff, faToggleOn } from "@fortawesome/free-solid-svg-icons";

const Chatting = ({ userObj }) => {
  const [chats, setChats] = useState([]);
  const [autoBtm, setAutoBtm] = useState(false);
  /* var time;
  function scroll() {
    time = setInterval(function () {
      var element = document.getElementById("chat_area");
      element.scrollTop = element.scrollHeight - element.clientHeight;
    }, 1000);
  }
  function stopScroll() {
    clearInterval(time);
  }
  const toggleBtm = () => {
    setAutoBtm((prev) => !prev);
    if (autoBtm === true) {
      scroll();
    } else {
      stopScroll();
    }
  };
  */
  useEffect(() => {
    dbService
      .collection("chats")
      .orderBy("createdAt")
      .onSnapshot((snapshot) => {
        const chatArray = snapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));
        setChats(chatArray);
      });
  }, []);
  const btm = () => {
    var element = document.getElementById("chat_area");
    element.scrollTop = element.scrollHeight - element.clientHeight;
  };
  /*{autoBtm ? (
        <>
          자동 아래로 스크롤
          <FontAwesomeIcon icon={faToggleOn} size="2x" onClick={toggleBtm} />
        </>
      ) : (
        <>
          자동 아래로 스크롤
          <FontAwesomeIcon icon={faToggleOff} size="2x" onClick={toggleBtm} />
        </>
      )} */
  return (
    <div className="container">
      <h1 className="buk center" style={{ fontSize: "50px" }}>
        말동무 이야기방
      </h1>
      <br />
      <h5 className="center" onClick={btm}>
        아래로
      </h5>
      <div style={{ marginTop: 30 }}>
        <div className="chat_area" id="chat_area">
          {chats.map((chat) => (
            <Chat
              userObj={userObj}
              key={chat.id}
              isOwner={chat.creatorId === userObj.uid}
              chatObj={chat}
            />
          ))}
        </div>
      </div>
      <br />
      <ChatFac userObj={userObj} />
    </div>
  );
};
export default Chatting;
