import { authService, dbService } from "fbase";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { v4 as uuidv4 } from "uuid";

export default ({ refreshUser, userObj }) => {
  const history = useHistory();
  const [newDisplayName, setNewDisplayName] = useState(userObj.displayName);
  const [pageId, setPageId] = useState("");
  const [pagesId, setPagesId] = useState("");
  const [pageOn, setPageOn] = useState(false);
  useEffect(() => {
    dbService.collection("pages").onSnapshot((snapshot) => {
      const chatArray = snapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
      setPagesId(chatArray);
    });
  }, []);

  const onLogOutClick = () => {
    authService.signOut();
    history.push("/");
    refreshUser();
  };
  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    setNewDisplayName(value);
  };
  const onSubmit = async (event) => {
    event.preventDefault();
    if (userObj.displayname !== newDisplayName) {
      await userObj.updateProfile({
        displayName: newDisplayName,
      });
      refreshUser();
    }
  };
  const emailclick = () => {
    alert(
      "오류 발생시 메뉴->건의사항 링크에 들어가서 오류나 건의 내용을 보내주세요\n(버전이 맞지 않으면 오류가 발생할 수 있음)"
    );
  };
  const page = async () => {
    const pageObjm = {
      name: userObj.displayName,
      idm: userObj.uid,
      id: uuidv4(),
    };
    //업데이트 db
    setPageOn(true);
  };
  return (
    <div className="container">
      <form onSubmit={onSubmit} className="profileForm">
        <input
          onChange={onChange}
          type="text"
          autoFocus
          placeholder="표시될 이름"
          value={newDisplayName}
          className="formInput"
        />
        <br />
        <input
          type="submit"
          value="프로필 갱신"
          className="formBtn buk"
          style={{
            marginTop: 10,
          }}
        />
        <br />
        <span className="center">
          이메일 : <span onClick={emailclick}>{userObj.email}</span>
          <br />
          <br />
          암호화된 이메일 :{" "}
          <span onClick={emailclick}>{userObj.lockEmail}</span>
        </span>
      </form>
      <br />
      <span className="center buk">
        오류나 건의사항이 있다면 전자우편을 클릭해 주세요
      </span>
      <br />
      <span className="center buk">
        내 페이지 링크 https://runningboy02.github.io/nwitter/p/
        {(userObj.uid + "").slice(0, 5)} (베타)
      </span>
      <br />
      <button onClick={page}>내 개인 페이지 업데이트</button>

      <br />
      <span className="center">{userObj.v}</span>
      <span className="formBtn cancelBtn logOut buk" onClick={onLogOutClick}>
        로그 아웃
      </span>
      <br />
      <span className="center buk help">도와주신 분들</span>
      <br />
      <p className="buk">
        <p>제작자 : 런닝보이</p>
        <p>이름 창조자 : 서브마린</p>
      </p>
    </div>
  );
};
