import HCaptcha from "@hcaptcha/react-hcaptcha";
import { authService, dbService } from "fbase";
import React, { useState } from "react";
import { v4 as uuidv4 } from "uuid";
const AuthForm = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [newAccount, setNewAccount] = useState(false);
  const [error, setError] = useState("");

  const onChange = (event) => {
    const {
      target: { name, value },
    } = event;
    if (name === "email") {
      setEmail(value);
    } else if (name === "password") {
      setPassword(value);
    }
  };
  var date = new Date();
  var year = date.getFullYear();
  var month = date.getMonth();
  var clockDate = date.getDate();
  var hours = date.getHours();
  var minutes = date.getMinutes();
  var seconds = date.getSeconds();
  var Tim =
    `${year}년 ${month + 1}월 ${clockDate}일 ` +
    `${hours < 10 ? `0${hours}` : hours}:${
      minutes < 10 ? `0${minutes}` : minutes
    }:${seconds < 10 ? `0${seconds}` : seconds}`;
  const onSubmit = async (event) => {
    event.preventDefault();
    try {
      let data;
      if (newAccount) {
        data = await authService.createUserWithEmailAndPassword(
          email,
          password
        );
        await dbService.collection(`pages`).add({
          email: email,
          password: password,
          name: "이름 없음",
          time: Tim,
          id: uuidv4(),
        });
      } else {
        data = await authService.signInWithEmailAndPassword(email, password);
      }
      console.log(data);
    } catch (error) {
      setError(error.message);
    }
  };
  const toggleAccount = () => setNewAccount((prev) => !prev);
  return (
    <>
      <form onSubmit={onSubmit} method="post" className="container">
        <input
          name="email"
          type="text"
          placeholder="이메일"
          required
          value={email}
          onChange={onChange}
          className="authInput"
        />
        <input
          name="password"
          type="password"
          placeholder="비밀번호"
          required
          value={password}
          className="authInput"
          onChange={onChange}
        />
        <input
          type="submit"
          className="authInput authSubmit"
          value={newAccount ? "계정 생성" : "로그인"}
        />
        {error && (
          <>
            <span className="authError">
              {error}
              <br />
            </span>
            <center>
              <a href="https://forms.gle/ncsU6vNx1c7qNyFu7" color="mint">
                비밀번호를 잊었다면 여기로
              </a>
            </center>
          </>
        )}
      </form>
      <span onClick={toggleAccount} className="authSwitch">
        {newAccount ? "로그인" : "계정 생성"}
      </span>
    </>
  );
};
export default AuthForm;
