import React, { useState } from "react";

function Chat({ chatObj, isOwner }) {
  return (
    <>
      {isOwner ? (
        <>
          <br />
          <div className="chat_area_own">
            <h5>&nbsp;{chatObj.text}</h5>
          </div>
        </>
      ) : (
        <>
          <br />
          <h1>{chatObj.creatorName}</h1>
          <div className="chat_area_y">
            <h5>&nbsp;{chatObj.text}</h5>
          </div>
        </>
      )}
    </>
  );
}
export default Chat;
