import React, { useEffect, useState } from "react";
import Crypto from "crypto-js";

//암호화 키 : LockString+정한 키
function Lock() {
  const [lock, setLock] = useState(true);
  const [text, setText] = useState("");
  const [ans, setAns] = useState("");
  const [keys, setKeys] = useState("");
  function locks(data, key) {
    return Crypto.AES.encrypt(data, key).toString();
  }
  function unLock(data, key) {
    return Crypto.AES.decrypt(data, key).toString(Crypto.enc.Utf8);
  }
  const onSubmit = (event) => {
    event.preventDefault();
    setAns(locks(text, keys));
  };
  const onSubmitm = (event) => {
    event.preventDefault();
    setAns(unLock(text, keys));
  };
  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    setText(value);
  };
  const onChangek = (event) => {
    const {
      target: { value },
    } = event;
    setKeys(value);
  };
  const changeMode = () => {
    setLock((prev) => !prev);
    setAns("");
    setText("");
    setKeys("");
  };
  const copy3 = () => {
    var obj = document.getElementById("aaa");
    if (obj !== "") {
      var range = document.createRange();
      range.selectNode(obj.childNodes[0]); //텍스트 정보를 Range 객체에 저장
      var sel = window.getSelection();
      sel.removeAllRanges(); //기존 선택정보 삭제
      sel.addRange(range); //텍스트 정보 선택
      document.execCommand("copy"); //복사
      window.alert("복사했습니다.");
      sel.removeRange(range); //선택 정보 삭제
    }
  };

  return (
    <div className="center">
      {lock ? (
        <>
          <h1 className="center">문자열 암호화</h1>
          <button onClick={changeMode}>복호화</button>
          <form onSubmit={onSubmit}>
            <br />
            <input
              type="text"
              onChange={onChange}
              placeholder="암호화 할 문자열"
              value={text}
              className="factoryInput__input"
              required
            />
            <br />
            <input
              type="text"
              placeholder="복호화 할때 필요한 키"
              onChange={onChangek}
              value={keys}
              className="factoryInput__input"
              required
            />
            <br />
            <input type="submit" value="암호화" />
          </form>
          <h5 id="answer">
            암호화된 문자열 : <a id="aaa">{ans}</a>
          </h5>
          <button onClick={copy3}>복사</button>
        </>
      ) : (
        <>
          <h1 className="center">문자열 복호화</h1>
          <button onClick={changeMode}>암호화</button>
          <form onSubmit={onSubmitm}>
            <br />
            <input
              type="text"
              onChange={onChange}
              placeholder="암호화 된 문자열"
              value={text}
              className="factoryInput__input"
              required
            />
            <br />
            <input
              type="text"
              placeholder="키"
              onChange={onChangek}
              value={keys}
              className="factoryInput__input"
              required
            />
            <br />
            <input type="submit" value="복호화" />
          </form>
          <h5>복호화된 문자열 : {ans}</h5>
        </>
      )}
    </div>
  );
}

export default Lock;
