import React, { useEffect, useState } from "react";
import AppRouter from "components/Router";
import { authService, dbService } from "fbase";
import Crypto from "crypto-js";

function App() {
  //이메일 암호화 키 : EmailLock
  const [init, setInit] = useState(false);
  const [userObj, setUserObj] = useState(null);
  const [pageObj, setPageObj] = useState(null);
  function lock(data, key) {
    return Crypto.AES.encrypt(data, key).toString();
  }
  let key = "EmailLock";
  useEffect(() => {
    dbService.collection("pages").onSnapshot((snapshot) => {
      const chatArray = snapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
      setPageObj(chatArray);
    });
    authService.onAuthStateChanged(async (user) => {
      if (user) {
        setUserObj({
          displayName: user.displayName,
          uid: user.uid,
          email: user.email,
          lockEmail: lock(user.email, key),
          updateProfile: (args) => user.updateProfile(args),
          v: "v 1.6",
          //버전 업데이트 시 이메일 발송
        });
      } else {
        setUserObj(null);
      }
      setInit(true);
    });
  }, []);
  const refreshUser = async () => {
    const user = authService.currentUser;
    setUserObj({
      displayName: user.displayName,
      uid: user.uid.refreshUser,
      email: user.email,
      v: "v 1.6",
      updateProfile: (args) => user.updateProfile(args),
    });
  };
  return (
    <>
      {init ? (
        <AppRouter
          refreshUser={refreshUser}
          isLoggedIn={Boolean(userObj)}
          userObj={userObj}
          pageObj={pageObj}
        />
      ) : (
        <span className="center">초기화중...</span>
      )}
    </>
  );
}

export default App;
