import React, { useEffect, useState } from "react";

function Info() {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [medal, setMedal] = useState("");
  const [news, setNews] = useState("");
  useEffect(async () => {
    await window
      .fetch(`https://api-gw.sports.naver.com/olympic/tokyo2020/countries/KOR`)
      .then((res) => res.json())
      .then((medal) => {
        setMedal(medal);
      })
      .catch((error) => {
        setError(error);
      });
    await window
      .fetch(
        `https://api-gw.sports.naver.com/news/rankings/popular/tokyo2020?page=1&pageSize=10`
      )
      .then((res) => res.json())
      .then((news) => {
        setNews(news);
        setLoading(false);
      })
      .catch((error) => {
        setError(error);
        setLoading(false);
      });
  });
  var Mcount = -1;
  var Ncount = 0;
  return (
    <>
      {loading ? (
        <>메달 정보 가져오는 중..</>
      ) : (
        <div className="center">
          <h1>{error}</h1>
          <h1>대한민국 전체 메달</h1>
          <br />
          <h5>금메달 : {medal.result.country.gold}개</h5>
          <h5>은메달 : {medal.result.country.silver}개</h5>
          <h5>동메달 : {medal.result.country.bronze}개</h5>
          <h5>전체 : {medal.result.country.total}개</h5>
          {medal.result?.discipline.map(() => {
            Mcount++;
            return (
              <>
                <br />
                <br />
                <h1>
                  대한민국 {medal.result?.discipline[Mcount]?.disciplineName}{" "}
                  메달
                </h1>
                <br />
                <h5>금메달 : {medal.result?.discipline[Mcount]?.gold}개</h5>
                <h5>은메달 : {medal.result?.discipline[Mcount]?.silver}개</h5>
                <h5>동메달 : {medal.result?.discipline[Mcount]?.bronze}개</h5>
                <h5>전체 : {medal.result?.discipline[Mcount]?.total}개</h5>
              </>
            );
          })}
        </div>
      )}
    </>
  );
}

export default Info;
