import React from "react";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTwitter } from "@fortawesome/free-brands-svg-icons";
import {
  faUser,
  faStickyNote,
  faTools,
  faRedo,
  faCompass,
  faComments,
  faMedal,
} from "@fortawesome/free-solid-svg-icons";
import { MobileView } from "react-device-detect";

const Navigation = ({ userObj }) => {
  return (
    <nav>
      <ul style={{ display: "flex", justifyContent: "center", marginTop: 50 }}>
        <li>
          <Link
            to="/"
            style={{
              marginLeft: 10,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              fontSize: 12,
            }}
          >
            <FontAwesomeIcon
              title="Home"
              icon={faTwitter}
              color={"red"}
              size="2x"
            />
            <span style={{ marginTop: 10 }}>게시판</span>
          </Link>
        </li>
        <li>
          <Link
            to="/profile"
            style={{
              marginLeft: 10,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              fontSize: 12,
            }}
          >
            <FontAwesomeIcon
              title="profile"
              icon={faUser}
              color={"#04AAFF"}
              size="2x"
            />
            <span style={{ marginTop: 10 }}>
              {userObj.displayName
                ? `${userObj.displayName}의 프로필`
                : "프로필"}
            </span>
          </Link>
        </li>
        <li>
          <Link
            to="/chat"
            style={{
              marginLeft: 10,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              fontSize: 12,
            }}
          >
            <FontAwesomeIcon
              title="Chat"
              icon={faComments}
              color={"#fff"}
              size="2x"
            />
            <span style={{ marginTop: 10 }}>말동무 이야기방β</span>
          </Link>
        </li>
        <li>
          <Link
            to="/notice"
            style={{
              marginLeft: 10,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              fontSize: 12,
            }}
          >
            <FontAwesomeIcon
              title="Notice"
              icon={faStickyNote}
              color={"#fff"}
              size="2x"
            />
            <span style={{ marginTop: 10 }}>공지사항</span>
          </Link>
        </li>
        <li>
          <Link
            to="/menu"
            style={{
              marginLeft: 10,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              fontSize: 12,
            }}
          >
            <FontAwesomeIcon
              title="menu"
              icon={faCompass}
              color={"#fff"}
              size="2x"
            />
            <span style={{ marginTop: 10 }}>메뉴</span>
          </Link>
        </li>
        <li>
          <Link
            to="/info"
            style={{
              marginLeft: 10,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              fontSize: 12,
            }}
          >
            <FontAwesomeIcon
              title="menu"
              icon={faMedal}
              color={"#fff"}
              size="2x"
            />
            <span style={{ marginTop: 10 }}>올림픽 메달 정보</span>
          </Link>
        </li>
      </ul>
    </nav>
  );
};
export default Navigation;
