import { dbService } from "fbase";
import React, { useEffect, useState } from "react";
import Nweet from "components/Nweet";
import NweetFactory from "components/NweetFactory";

const Home = ({ userObj }) => {
  const [nweets, setNweets] = useState([]);
  const [likeNweets, setLikeNweets] = useState([]);
  const [likeMode, setLikeMode] = useState(true);
  useEffect(() => {
    dbService
      .collection("nweets")
      .orderBy("like", "desc")
      .onSnapshot((snapshot) => {
        const nweetArray = snapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));
        setNweets(nweetArray);
      });
    dbService
      .collection("nweets")
      .orderBy("like", "desc")
      .limit(5)
      .onSnapshot((snapshot) => {
        const nweetArray = snapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));
        setLikeNweets(nweetArray);
      });
  }, []);
  const toggleMode = () => {
    setLikeMode((prev) => !prev);
  };
  return (
    <div className="container">
      {likeMode ? (
        <>
          <h1 className="center">전체 보기</h1>
          <br />
          <NweetFactory userObj={userObj} />
          <br />
          <a className="center" onClick={toggleMode}>
            좋아요 상위 5개 보기
          </a>
          <div style={{ marginTop: 30 }}>
            {nweets.map((nweet) => (
              <Nweet
                userObj={userObj}
                key={nweet.id}
                nweetObj={nweet}
                isOwner={nweet.creatorId === userObj.uid}
              />
            ))}
          </div>
        </>
      ) : (
        <>
          <h1 className="center">좋아요 상위 5개 보기</h1>
          <br />
          <NweetFactory userObj={userObj} />
          <br />
          <a className="center" onClick={toggleMode}>
            전체 보기
          </a>
          <div style={{ marginTop: 30 }}>
            {likeNweets.map((nweet) => (
              <Nweet
                userObj={userObj}
                key={nweet.id}
                nweetObj={nweet}
                isOwner={nweet.creatorId === userObj.uid}
              />
            ))}
          </div>
        </>
      )}
    </div>
  );
};
export default Home;
