import React from "react";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import Auth from "routes/Auth";
import Home from "routes/Home";
import Profile from "routes/Profile";
import Navigation from "components/Navigation";
import Notice from "routes/Notice";
import Rrd from "routes/rrd";
import Menu from "routes/Menu";
import Re from "routes/Re";
import Report from "routes/Report";
import Chatting from "routes/Chatting";
import Info from "routes/Info";
import Lock from "routes/Lock";
import Pages from "routes/page";

const AppRouter = ({ refreshUser, isLoggedIn, userObj, pageObj }) => {
  return (
    <Router>
      {isLoggedIn && <Navigation userObj={userObj} />}
      <Switch>
        {isLoggedIn ? (
          <div
            style={{
              maxWidth: 890,
              width: "100%",
              margin: "0 auto",
              marginTop: 80,
              display: "flex",
              justifyContent: "center",
            }}
          >
            <Route exact path="/">
              <Home userObj={userObj} />
            </Route>
            <Route exact path="/profile">
              <Profile userObj={userObj} refreshUser={refreshUser} />
            </Route>
            <Route exact path="/notice">
              <Notice userObj={userObj} />
            </Route>
            <Route exact path="/rrd">
              <Rrd userObj={userObj} refreshUser={refreshUser} />
            </Route>
            <Route exact path="/menu">
              <Menu userObj={userObj} />
            </Route>
            <Route exact path="/re">
              <Re userObj={userObj} />
            </Route>
            <Route exact path="/report">
              <Report userObj={userObj} />
            </Route>
            <Route exact path="/chat">
              <Chatting userObj={userObj} />
            </Route>
            <Route exact path="/info">
              <Info userObj={userObj} />
            </Route>
            <Route exact path="/lock">
              <Lock userObj={userObj} />
            </Route>
            {pageObj &&
              pageObj.map((pageObj, index) => {
                return (
                  <Route exact path={"/p/" + (pageObj.idm + "").slice(0, 5)}>
                    <Pages pageObj={pageObj} key={index} />
                  </Route>
                );
              })}
          </div>
        ) : (
          <>
            <Route exact path="/">
              <Auth userObj={userObj} />
            </Route>
            {pageObj &&
              pageObj.map((pageObj, index) => {
                return (
                  <Route exact path={"/p/" + (pageObj.idm + "").slice(0, 5)}>
                    <Pages pageObj={pageObj} key={index} />
                  </Route>
                );
              })}
          </>
        )}
      </Switch>
    </Router>
  );
};

export default AppRouter;
