import { dbService } from "fbase";
import React, { useState } from "react";

function ChatFac({ userObj }) {
  const [chat, setChat] = useState("");
  const [canChat, setCanChat] = useState(true);
  //await dbService.collection("chats").add(ChatObj);
  const onSubmit = async (event) => {
    event.preventDefault();
    if (chat !== "") {
      // 이메일 가리기
      var creatorEmail = null;
      if (userObj.email !== null) {
        var a = userObj.email.split("@")[0];
        var b = "*".repeat(userObj.email.split("@")[1].split(".")[0].length);
        var c = userObj.email.split("@")[1].split(".")[1];
        creatorEmail = a + "@" + b + "." + c;
      }
      //   시간
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth();
      var clockDate = date.getDate();
      var hours = date.getHours();
      var minutes = date.getMinutes();
      var seconds = date.getSeconds();
      var Tim =
        `${year}년 ${month + 1}월 ${clockDate}일 ` +
        `${hours < 10 ? `0${hours}` : hours}:${
          minutes < 10 ? `0${minutes}` : minutes
        }:${seconds < 10 ? `0${seconds}` : seconds}`;
      // 업로드
      const chatObj = {
        text: chat,
        createdAt: Date.now(),
        creatorId: userObj.uid,
        creatorEmail: creatorEmail,
        creatorName: userObj.displayName,
        createdTime: Tim,
        adminEmail: userObj.email,
      };
      setChat("");
      await dbService.collection("chats").add(chatObj);
      var element = document.getElementById("chat_area");
      element.scrollTop = element.scrollHeight - element.clientHeight;
    }
  };
  let login = <h5>로그인 하세요.</h5>;
  const onChange = (event) => {
    if (userObj.email === null || userObj.displayName === null) {
      setCanChat(false);
    }
    if (
      canChat === false &&
      userObj.email !== null &&
      userObj.displayName === null
    ) {
      login = (
        <div className="center">
          <br />
          <h5>이름을 설정하세요.</h5>
          <a href="/#/profile" style={{ color: "#c0c0c0" }}>
            이름 설정 바로가기
          </a>
        </div>
      );
    }
    const {
      target: { value },
    } = event;
    setChat(value);
  };
  return (
    <>
      {canChat ? (
        <form onSubmit={onSubmit}>
          <div className="chat__container">
            <input
              className="chat__input"
              type="text"
              placeholder="엔터키로 전송"
              onChange={onChange}
              value={chat}
            />
            <input type="submit" value="&rarr;" className="chat__arrow" />
          </div>
        </form>
      ) : (
        <>
          <h1 className="center">채팅 권한 없음</h1>
          {login}
        </>
      )}
    </>
  );
}
export default ChatFac;
